@font-face {font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,); /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'), 
  url(data:application/font-woff;base64,) format('woff'),
  url(data:application/x-font-ttf;base64,) format('truetype'), 
  url(data:image/svg+xml;base64,#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-home:before { content: "\E626"; }

.icon-user:before { content: "\EC52"; }

.icon-indent:before { content: "\E62B"; }

.icon-outdent:before { content: "\E62C"; }

.icon-caret-down:before { content: "\E631"; }

* {
  margin: 0;
  padding: 0;
}

*, :after, :before {
  webkit-box-sizing: border-box;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-size: 14px;
}

@media all and (max-width: 768px) {
  html {
    font-size: 12px;
  }
}

a {
  text-decoration: none;
  color: #777777;
  display: block;
}

ul {
  list-style: none;
  margin-bottom: 0;
}

#app {
  width: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
}

/* top navbar style start */

.container .topbar-wrap {
  width: 100%;
  height: 60px;
  line-height: 60px;
  background: #373d41;
  padding: 0px;
  min-width: 900px;
  white-space:nowrap;
}

.container .topbar-wrap .topbar-btn {

  color: #fff;

}

.container .topbar-wrap .topbar-logo {
  width: 250px;
  height: 60px;
  float: left;
  background-color: #3D4A53;
}

.container .topbar-wrap .topbar-logos {
  float: left;
  width: 128px;
  line-height: 48px;
  font-size: 14px;

}

.container .topbar-wrap .topbar-logo img, .container .topbar-wrap .topbar-logos img {
  height: 35px;
  margin-top: 12px;
  margin-left:32px;
}

.container .topbar-wrap .topbar-title {
  /*width:863px ;*/
  float: left;
  text-align: left;
}

/*.el-row .el-col .el-menu-demo{*/

/*min-width: 800px;*/

/*background-color: #00C1DE;*/

/*}*/

.topbar-title .el-menu--horizontal {
  background-color: transparent;
}

.el-menu--horizontal > .el-menu-item:not(.is-disabled):hover, .el-menu--horizontal > .el-menu-item:not(.is-disabled):focus, .el-menu--horizontal > .el-menu-item.is-active {
  color: #fff;
  background-color: transparent;
  /*border-bottom: 2px solid #409EFF !important;*/
}

.topbar-title .el-menu--horizontal > .el-menu-item {
  height: 60px;
  line-height: 60px;
  color: #fff;
}

.el-menu-item .iconfont {
  margin-right: 5px;
  display: inline-block;
  width: 24px;
  text-align: center;
  font-size: 18px;
  vertical-align: middle;
}

.container .topbar-wrap .topbar-account {
  float: right;
  padding-right: 12px;
}

.container .topbar-wrap .topbar-timer {
  display: inline-block;
}

.container .topbar-wrap .topbar-timer span {
  display: inline-block;
  vertical-align: middle;
}

.container .topbar-wrap .topbar-timer .login-name {
  margin: 0 6px;
  font-style: normal;
}

.container .topbar-wrap .userinfo-inner {
  cursor: pointer;
  color: #fff;
  padding-left: 10px;
}

.container .topbar-wrap .userinfo-inner img {
  margin-left: 6px;
  width: 42px;
  height: 42px;
  border: 1px solid #504d4d;
  border-radius: 50%;
  vertical-align: middle;
}

/* top navbar style end */

/* left sidebar style start */

.container aside {
  min-width: 50px;
  background: #333744;
}

.container aside::-webkit-scrollbar {
  display: none;
}

.container aside .menu-toggle {
  background: #4A5064;
  text-align: center;
  color: white;
  height: 26px;
  line-height: 30px;
}

.container aside .menu-toggle .iconfont:hover {
  cursor: pointer;
}

aside .el-menu-item, aside .el-submenu__title {
  color: #fff;
  text-align: left;
}

aside .el-menu-item:hover, aside .el-submenu .el-menu-item:hover, aside .el-submenu__title:hover {
  background-color: #7ed2df;
}

aside .el-submenu .el-menu-item {
  background-color: #333744;
}

aside .el-submenu .el-menu-item:hover {
  background-color: #4A5064;
}

aside .el-submenu .el-menu-item.is-active, aside .el-menu-item.is-active,
aside .el-submenu .el-menu-item.is-active:hover, aside .el-menu-item.is-active:hover {
  background-color: #00C1DE;
  color: #fff;
}

.container aside.showSidebar {
  overflow-x: hidden;
  overflow-y: auto;
}

.container aside .el-menu {
  height: 100%; /*calc()*/
  height: calc(100% - 80px);
  border-radius: 0px;
  background-color: #333744;
  border-right: 0px;
}

.container aside .el-submenu .el-menu-item {
  min-width: 60px;
}

.container aside .el-menu {
  width: 189px;
}

.container aside .el-menu--collapse {
  width: 60px;
}

.container aside .el-menu .el-menu-item, .container aside .el-submenu .el-submenu__title {
  height: 46px;
  line-height: 46px;
}

.container aside .el-menu-item:hover, .container aside .el-submenu .el-menu-item:hover, .container aside .el-submenu__title:hover {
  background-color: #7ed2df;
}

/* left sidebar style end */

.container .main {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  position: absolute;
  top: 50px;
  bottom: 0px;
  overflow: hidden;
}

.container .warp-main {
  padding-top: 10px;
  padding-bottom: 10px;
}

.container .content-container {
  background: #f2f2f2;
  -ms-flex: 1;
  -webkit-box-flex: 1;
          flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.container .content-container .content-wrapper {
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.grid-content {
  border-radius: 2px;
  background-color: #fff;
}

.grid-content .toolbar {
  padding: 10px 10px 0 10px;
}

.grid-content .el-pagination {
  padding: 15px;
  text-align: right;
}

.table-wrapper {
  border-top: 1px solid #ebeef5;
}

/* bottom footer style end */

/* scrollbar style start */

::-webkit-scrollbar {
  background: transparent;
  width: 10px;
  height: 10px
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #e1e1e1;
  width: 6px;
  height: 6px;
  border: 2px solid transparent;
  background-clip: content-box
}

::-webkit-scrollbar-track {
  border-radius: 5px;
  background-color: #fafafa
}

/* scrollbar style end */

/* sidebar style start */

.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

.slide-fade-leave-active {
  -webkit-transition: all .3s cubic-bezier(1, .5, .8, 1);
  transition: all .3s cubic-bezier(1, .5, .8, 1);
}

.slide-fade-enter, .slide-fade-leave-to {
  -webkit-transform: translateX(600px);
          transform: translateX(600px);
}

/* sidebar style end */

/* pagination style start*/

.el-pagination.is-background .btn-next,.el-pagination.is-background .btn-prev,.el-pagination.is-background .el-pager li{
  margin: 0;
  padding: 0;
  height: 40px;
  line-height: 40px;
  min-width: 40px;
}

.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #68819E;
  color: #fff;
}

/* pagination style end*/

